import Bus from 'framebus'
import { injectWithAllowlist } from 'inject-stylesheet'
import { inIframe } from '@/lib/inIframe'
import { ALLOWED_STYLES, events } from '@/constants'
import errors from '@/errors'
import { isAllowedField, assembleIframes } from "@/services";
import { InputComponents } from "@/components/InputComponents";
import { HostedFieldsOptionModel } from '@/models/HostedFieldsOptionModel';
import { DnaPaymentsError } from '@/models/DnaPaymentsError';
import { FieldType } from '@/models/FieldType'
import { BaseInput } from '@/components/BaseInput'
import { CreditCardForm } from './CreditCardForm'
import { Client } from 'payment-core'

export class HostedFieldsInternal {

    field: FieldType
    bus: Bus
    component: BaseInput
    model: CreditCardForm

    constructor(field: string, instanceId: string) {
        if (!inIframe()) {
            throw new DnaPaymentsError(errors.NOT_IN_IFRAME)
        }

        if (!isAllowedField(field)) {
            throw new DnaPaymentsError(errors.NOT_ALLOWED_FIELD)
        }

        this.field = field
        this.bus = new Bus({ channel: instanceId })
        console.log('field', field, 'instanceId', instanceId)
        console.log('bus', this.bus)

        this.bus.emit(events.FRAME_READY, { field }, this.initialize.bind(this))
        this.attachBusEvents()
    }

    initialize(options: HostedFieldsOptionModel) {
        injectWithAllowlist(options.styles, ALLOWED_STYLES)
        const fieldKey = this.field

        // synchronize model for all iframes
        if (!this.model) {
            const client = new Client({ isTest: options.isTest, token: options.authToken })
            client.init()
            this.model = new CreditCardForm(options, this.bus, client)
            const iframes = assembleIframes(window.parent)
            iframes.forEach((iframe) => {
                iframe.hostedFieldsInternal.model = this.model
            })
        }

        this.component = new InputComponents[fieldKey]({ fieldKey, model: this.model })
        document.body.appendChild(this.component.container)
    }

    attachModelEvents() {
        // this.model.on('focus', ({ field }) => {
        //     console.log('emit focus', field)
        //     this.bus.emit(externalEvents.FOCUS, { field })
        // })
    }
    
    attachBusEvents() {
        this.bus.on(events.SUBMIT, (data: { paymentData: any }, reply) => {
            if (!this.model.formState.isSubmitting) {
                this.model.formState.isSubmitting = true
    
                this.model.submit(data.paymentData)
                    .then((result) => {
                        this.model.formState.isSubmitting = false
                        console.log('internal result', result)
                        reply([null, result])
                    })
                    .catch((err) => {
                        this.model.formState.isSubmitting = false
                        console.log('internal err', err)
                        reply([err.toJSON ? err.toJSON() : err])
                    })
            }
        })

        this.bus.on(events.CLEAR, () => {
            if (!this.model.formState.isClearing) {
                this.model.formState.isClearing = true
                this.model.clear()
                setTimeout(() => this.model.formState.isClearing = false, 0)
            }
        })
    }

    async onSubmit() {
        const isValid = await this.model.validateAll()

    }
}
