export function assembleIframes(parentWindow: Window) {
    const frames:Window[] = []
    for (let i = 0; i < parentWindow.frames.length; i++) {
        const frame = parentWindow.frames[i]
        try {
            if (frame.location.href === window.location.href) {
                frames.push(frame)
            }
        } catch {}
    }
    return frames
}