import RestrictedInput from 'restricted-input'
import { ALLOWED_FIELDS, externalEvents, PREFIX_FIELD_NAME } from "@/constants"
import { FieldKeyType } from '@/types'
import { setAttributes } from "../lib/setAttributes"
import { CreditCardForm } from '../classes/CreditCardForm'

export class BaseInput {

    inputType = 'tel'
    maxLength: number = null
    pattern: string = ' '
    container: DocumentFragment
    element: HTMLInputElement
    formatter: RestrictedInput
    
    constructor(private options: BaseInputOptions) {}

    get model() { return this.options.model }
    get fieldKey() { return this.options.fieldKey }
    get fieldState() { return this.model.fieldsState[this.fieldKey] }

    protected constructElement() {
        const { fieldKey, model: { configuration }} = this.options
        const { defaultValue, placeholder } = configuration.fields[fieldKey]
        const { name, autocomplete } = ALLOWED_FIELDS[fieldKey]

        const input = document.createElement('input')

        setAttributes(input, {
            type: this.inputType,
            name: `${PREFIX_FIELD_NAME}${name}`,
            maxlength: this.maxLength,
            placeholder,
            autocomplete,
            value: defaultValue
        })  

        this.container = document.createDocumentFragment()
        this.element = input
        this.formatter = new RestrictedInput({
            element: this.element,
            pattern: this.pattern
        })

        if (defaultValue) {
            this.model.setValue(fieldKey, defaultValue)
        }
        
        this.addDomEventListeners()
        this.addModelEventListeners()
        this.container.appendChild(this.element)
    }

    render() {
        const state = this.fieldState
        this.element.classList.toggle('valid', state.isValid && state.isTouched)
        this.element.classList.toggle('invalid', !state.isValid && state.isTouched)

        if (this.maxLength) {
            this.element.setAttribute('maxlength', this.maxLength + '')
        }
    }

    setAttributes(attributes: Record<string, any>) {
        setAttributes(this.element, attributes)
    }

    protected getUnformattedValue() {
        return this.formatter.getUnformattedValue()
    }

    protected addModelEventListeners() {
        this.model.on(externalEvents.VALIDITY_CHANGE, ({ fieldKey }) => {
            if (fieldKey === this.fieldKey) {
                this.render()
            }
        })

        this.model.on(externalEvents.CHANGE, ({ fieldKey }) => {
            if (fieldKey === this.fieldKey) {
                this.render()
            }
        })

        this.model.on(externalEvents.CLEAR, ({ fieldKey }) => {
            if (fieldKey === this.fieldKey) {
                this.element.value = ''
                this.render()
            }
        })
    }

    protected addDomEventListeners() {
        this.element.addEventListener('input', () => {
            this.model.setValue(this.fieldKey, this.getUnformattedValue())
        })

        this.element.addEventListener('focus', () => {
            this.model.setFocus(this.fieldKey, true)
        })

        this.element.addEventListener('blur', () => {
            this.model.setFocus(this.fieldKey, false)            
        })
    }
}

export interface BaseInputOptions {
    fieldKey: FieldKeyType
    model: CreditCardForm
}

