import Bus from 'framebus'
import EventEmitter from '@braintree/event-emitter'
import { Client } from 'payment-core';
import { HostedFieldsOptionModel } from '../models/HostedFieldsOptionModel';
import { events, externalEvents } from '../constants';
import { FieldStateType, FieldKeyType, InputEventDataType, InputEventNameType, InputEventCallbackType } from '@/types';
import { DnaPaymentsError } from '../models/DnaPaymentsError';
import { DnaPaymentsErrorType } from '../models/DnaPaymentsErrorType';
import errors from '../errors';

export class CreditCardForm extends EventEmitter {

    private _fieldsState: Partial<Record<FieldKeyType, { shouldValidate: boolean, isValidated: boolean }>> = {}
    fieldsState: Partial<Record<FieldKeyType, FieldStateType>> = {}
    fieldsValue: Partial<Record<FieldKeyType, string>> = {}
    formState = {
        isClearing: false,
        isSubmitting: false,
    }

    get fieldKeys() {
        return  Object.keys(this.configuration.fields) as FieldKeyType[]
    }
    
    constructor(
        public configuration: HostedFieldsOptionModel,
        public bus: Bus,
        public client: Client
    ) {
        super()
        this.init()
        // const fieldKeys = Object.keys(configuration.fields)

        // fieldKeys.forEach((fieldKey) => {
        //     this.fieldsState[fieldKey] = {
        //         isFocused: false,
        //         isTouched: false,
        //         isValid: false,
        //         isEmpty: true,
        //         errorText: null
        //     }
        // })

        // fieldKeys.forEach((fieldKey) => {
        //     this._fieldsState[fieldKey] = {
        //         shouldValidate: false
        //     }
        // })

        // fieldKeys.forEach((fieldKey) => {
        //     this.fieldsValue[fieldKey] = ''
        // })
    }

    setFocus(fieldKey: FieldKeyType, isFocued: boolean) {
        const state = this.fieldsState[fieldKey]
        if (!isFocued) {
            state.isFocused = false
            if (this._fieldsState[fieldKey].shouldValidate) {
                this.validate(fieldKey)
            }
            this.emitEvent(fieldKey, externalEvents.BLUR)
        } else {
            Object.keys(this.fieldsState).forEach((fieldStateKey: FieldKeyType) => {
                this.fieldsState[fieldStateKey].isFocused = fieldStateKey === fieldKey
            })
            this.emitEvent(fieldKey, externalEvents.FOCUS)
        }
    }

    async validateAll() {
        const results = await Promise.all(this.fieldKeys.map((fieldKey) => this.validate(fieldKey)))
        return results.every(v => v)
    }

    async validate(fieldKey: FieldKeyType) {
        const _state = this._fieldsState[fieldKey]
        const state = this.fieldsState[fieldKey]
        const value = this.fieldsValue[fieldKey]
        const prevIsValid = state.isValid

        let result 
        switch (fieldKey) {
            case 'cardholderName':
                result = this.client.isValidCardHolder(value)
                break
            case 'cardNumber':
                result = await this.client.isValidCardNumber(value)
                break
            case 'expirationDate':
                console.log('value', value)
                result = this.client.isValidExpiryDate(value)
                break
            case 'cvv':
                result = this.client.isValidSecureCode(value)
                break
            default:
                result = false
        }

        if (typeof result === 'object') {
            state.isValid = false
            state.error = new DnaPaymentsError({
                type: DnaPaymentsErrorType.INTERNAL,
                code: result.code,
                message: result.message
            })
        } else {
            state.isValid = result
            state.error = null
        }

        state.isTouched = true

        if (!_state.isValidated || prevIsValid !== state.isValid) {
            this.emitEvent(fieldKey, externalEvents.VALIDITY_CHANGE)
        }

        _state.isValidated = true
        _state.shouldValidate = true

        // console.log('fieldsState', this._fieldsState, this.fieldsState)
        return state.isValid
    }

    setValue(fieldKey: FieldKeyType, value: string) {
        const state = this.fieldsState[fieldKey]
        this.fieldsValue[fieldKey] = value
        state.isEmpty = Boolean(value)
        this._fieldsState[fieldKey].shouldValidate = true
        this.emitEvent(fieldKey, externalEvents.CHANGE)
    }

    init() {
        this.fieldKeys.forEach((fieldKey) => {
            if (!this.fieldsState[fieldKey]) {
                this.fieldsState[fieldKey] = {} as any
            }
            Object.assign(this.fieldsState[fieldKey], {
                isFocused: false,
                isTouched: false,
                isValid: false,
                isEmpty: true,
                error: null
            })
        })

        this.fieldKeys.forEach((fieldKey) => {
            if (!this._fieldsState[fieldKey]) {
                this._fieldsState[fieldKey] = {} as any
            }
            Object.assign(this._fieldsState[fieldKey], {
                shouldValidate: false,
                isValidated: false
            })
        })

        this.fieldKeys.forEach((fieldKey) => {
            this.fieldsValue[fieldKey] = ''
        })
    }

    clear() {
        this.init()
        this.fieldKeys.forEach((fieldKey) => {
            this.emitEvent(fieldKey, externalEvents.CLEAR)
        })
    }

    async submit(paymentData: any) {
        const isValid = await this.validateAll()

        if (!isValid) {
            throw new DnaPaymentsError({
                type: DnaPaymentsErrorType.CUSTOMER,
                code: 'NOT_VALID_CARD_DATA',
                message: 'Not valid card data'
            })
        }

        const cardData = {
            expiryDate: this.fieldsValue.expirationDate,
            cardNumber: this.fieldsValue.cardNumber,
            cvv: this.fieldsValue.cvv,
            terminal: this.configuration.terminal
        }
        console.log('paymentData', paymentData, 'cardData', cardData)
        const { result, error } = await this.client.cryptoPay(paymentData, cardData)
        this.formState.isSubmitting = false

        if (error) {
            throw errors.internalError(error)
        }

        return result
    }

    emitEvent(fieldKey: FieldKeyType, eventType: InputEventNameType) {
        const payload = {
            fieldKey,
            fieldsState: this.fieldsState
        }
        const eventData: InputEventDataType = {
            payload,
            type: eventType
        }
        this._emit(eventType, payload)
        this.bus.emit(events.INPUT_EVENT, eventData)
    }

    on(eventName: InputEventNameType, callback: InputEventCallbackType) {
        super.on(eventName, callback)
    }

    // _emit(eventName: EventNameType, data: EventDataType) {
    //     super._emit(eventName, data)
    // }
}
