import { BaseInput, BaseInputOptions } from "./BaseInput";

export class CvvInput extends BaseInput {
    maxLength = 4
    pattern = '{{9999}}'

    constructor(options: BaseInputOptions) {
        super(options)
        this.constructElement()
    }

    protected constructElement() {
        super.constructElement()
        this.element.style.fontFamily = 'password'
    }
}