import { BaseInput, BaseInputOptions } from "./BaseInput";

export class ExpirationDateInput extends BaseInput {
    inputType = 'text'
    pattern = '{{99}} / {{99}}'

    constructor(options: BaseInputOptions) {
        super(options)
        this.constructElement()
    }

    protected getUnformattedValue() {
        const value = this.element.value || ''
        return value.replace(/\s/g, '')
    }
}