import { BaseInput, BaseInputOptions } from "./BaseInput";

export class CardHolderNameInput extends BaseInput {
    inputType = 'text'
    maxLength = 255
    pattern = '{{' + Array(this.maxLength + 1).join('*') + '}}';

    constructor(options: BaseInputOptions) {
        super(options)
        
        this.constructElement()
        this.element.style.textTransform = 'uppercase'
    }

    getUnformattedValue() {
        const value = super.getUnformattedValue()
        return value.toLocaleUpperCase()
    }
}